@import url(https://fonts.googleapis.com/css2?family=Asap&display=swap);
* {
	margin: 0;
}

body {
	margin: 0;
	font-family: Roboto, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

.header {
	display: flex;
	position: sticky;
	z-index: 1;
	top: 0;
	align-items: center;
	justify-content: space-between;
	padding: 20px;
	background-color: #3b424c;
	color: white;
	box-shadow: 0px 4px 18px 1px rgb(0 0 0 / 50%),
		0px 4px 5px 0px rgb(0 0 0 / 14%), 0px 1px 10px 0px rgb(0 0 0 / 12%);
}

.header__left > a {
	text-decoration: none;
	color: white;
	font-size: 20px;
}

.header__left > a:hover {
	text-decoration: underline;
}

.header__right {
	display: flex;
	align-items: center;
}

.header__icons {
	margin-right: 20px;
	display: flex;
	align-items: center;
}

.header__icons > a > i {
	margin-right: 15px;
	color: white;
	cursor: pointer;
	transition: all 0.3s ease;
}

.header__icons > a > i:hover {
	opacity: 0.7;
}

.header__avatar {
	object-fit: contain;
	height: 35px !important;
	width: 35px !important;
	cursor: pointer;
	transition: all 0.3s ease;
	margin-left: 10px;
	margin-right: -20px;
}

.blog {
	display: inline-flex;
	border-radius: 10px;
	background-color: rgb(235, 235, 235);
	padding: 5px;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	min-width: 500px;
	margin: 20px;
	transition: -webkit-transform 0.6s;
	transition: transform 0.6s;
	transition: transform 0.6s, -webkit-transform 0.6s;
	text-align: left;
	-webkit-animation-timing-function: ease-in-out;
	        animation-timing-function: ease-in-out;
	-webkit-animation: blog;
	        animation: blog;
	-webkit-animation-duration: 0.7s;
	        animation-duration: 0.7s;
	transition: all 0.5s ease;
	cursor: pointer;
}

.blog:hover {
	background-color: rgb(207, 207, 207);
}

@-webkit-keyframes blog {
	0% {
		-webkit-transform: scale(0);
		        transform: scale(0);
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

@keyframes blog {
	0% {
		-webkit-transform: scale(0);
		        transform: scale(0);
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

.blog:hover {
	-webkit-transform: scale(1.04);
	        transform: scale(1.04);
}

.blog__thumbnail {
	object-fit: cover;
	height: 200px;
	width: 200px;
	border-radius: 10px;
}

.blog__content {
	margin-left: 20px;
}

.blog___title {
	margin-top: 30px;
	font-size: 16px;
}

.blog__timestamp {
	color: gray;
	margin-top: 6px;
	font-size: 15px;
}

.blog__summary {
	margin-top: 7px;
	color: #6d6d74;
	font-size: 15px;
	font-weight: 400;
	margin-bottom: 20px;
}

.blog__readMore {
	color: #3495df;
	text-decoration: none;
}

.blog__readMore:hover {
	text-decoration: underline;
}

.blog__author {
	margin-top: 10px;
}

.blogs {
	margin-top: 40px;
	text-align: center;
}

.fa-plus {
	font-size: 14px !important;
}

.blogs > a {
	text-decoration: none;
}

.addBlog {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 40px;
}

.addBlog__details {
	margin-bottom: 20px;
}

.addBlog__details > h3 {
	font-size: 17px;
	font-weight: 400;
	margin-bottom: 10px;
}

.addBlog__details > input,
.addBlog__details > textarea {
	width: 350px;
	height: 20px;
	outline: none;
	border: 1px solid lightgray;
	margin-bottom: 10px;
}

.addBlog__details > textarea {
	height: 100px;
}

.fa-check {
	font-size: 15px !important;
}


.blogDisplay {
	display: grid;
	place-items: left;
	margin-top: 30px;
	margin-left: 30px;
}

.blogDisplay__title {
	font-family: Signifier;
	font-size: 48px;
	font-weight: 400;
	text-align: left;
}

.blogDisplay__thumbnail {
	object-fit: contain;
	margin-left: 5px;
	margin-bottom: 10px;
}

.blogDisplay__content {
	font-family: Asap, sans-serif;
	font-size: 18px;
	margin: 30px 0;
}

.blogDisplay__author {
	text-align: left;
	font-size: 17px;
	margin-bottom: 30px;
}

.blogDisplay > hr {
	margin-bottom: 10px;
}

.blogDisplay__comments > h3 {
	font-weight: 400;
}

.blogDisplay__commentsContainer {
	margin-top: 20px;
}

.blogDisplay__comment {
	display: flex;
	align-items: center;
	border-bottom: 1px solid lightgray;
	padding-bottom: 10px;
	margin-top: 5px;
}

.blogDisplay__comment > p {
	margin-left: 10px;
}

.blogDisplay__addComment {
	margin-top: -10px;
	margin-bottom: 20px;
}

.blogDisplay__addComment > input {
	border: 1px solid lightgray;
	outline: none;
	height: 25px;
}

.blogDisplay__addComment > button {
	display: none;
}

